.navBar-Small-Screen {
   width: 100%;
   background-color: white;
   color: #333;
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   display: none;
   height: 0;
 }
 .Navs-display-direction-Small-Screen {
   display: flex;
   flex-direction: column;
   float: right;
 }
 .button-decoration-Small-Screen {
   color:  rgb(3, 3, 3);
   font-size: large;
   background: transparent;
   cursor: pointer;
   outline: none;
   border: none;
   padding-right: 30px;
   padding-top: 15px;
 }
 .links-Small-Screen {
  height: 0;
  overflow: hidden;
  list-style-type: none;
  }
  .show-nav-Small-Screen {
    height: auto;
  }
  
@media screen and (max-width: 992px) {
   .Navbar-One-Style{
       display: flex;
       flex-direction: column;
   } 
   .Navbar-Part-Two-Style{
       display: flex;
       flex-direction: column;
       width: auto;
   }
   .navBar-Small-Screen {
       display: inline;
   }
   .navBar-Big-Screen{
       display: none;
   }
 }

.About-Style{
    display: flex;
    flex-direction: row;
    background-color: #202020;
    color: white;
}
.About-Intro-Style{
    padding: 2%;
    width: 50%;
}
.About-Intro-Style > h1{
    font-family: 'Amiri', serif;
    color:rgb(248, 167, 37);
}
.About-Intro-Style > p{
    padding: 2%;
}
.About-Fields-Style{
    padding: 2%;
    display: grid;
    grid-template-columns: auto auto auto;
}
.About-Fields-imgs-Style{
    position: relative;
}
.hover-button--on{
    position: absolute;
    display: none;
}
.hover-button .hover-button--on,
.hover-button:hover .About-Fields-imgs-Style {
    opacity: 0.8;
}

.hover-button:hover .hover-button--on {
  display: inline;
  opacity: 0.9;
  color: rgb(248, 167, 37);
  font-family: 'Amiri', serif;
  font-size: 35px;
}
